import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        search: '',
        products: [],
    },
    getters: {
        price(state) {
            return state.products
                .reduce((res, item) => res + parseInt(item.price * item.count), 0)
                .toFixed(2);
        }
    },
    mutations: {
        search(state, value) {
            state.search = value
        },
        addItem(state, data) {
            if (isNaN(data.price)) {
                return alert('无法加入购物车，商品无价格')
            }

            // console.log('addItem')
            let index = state.products.findIndex(product => {
                return product._id === data._id
            })

            if(index === -1) {
                state.products.push({...data, count: 1})
            } else {
                state.products[index].count++
            }

        },
        deleteItem(state, data) {
            // console.log('deleteItem')
            let index = state.products.findIndex(item => item._id === data._id)
            if (index === -1) {
                alert('不在购物车中，不能取消！')
            }else{
                state.products[index].count--;
                if (state.products[index].count <= 0) {
                    state.products.splice(index, 1);
                }
            }
        },
        //取消购买
        reset(state) {
            state.products = []
        }
    },
    actions: {}
})